import { Breadcrumbs, Button, IconButton, List, ListItem, ListItemText, Modal, Paper, Typography } from "@material-ui/core";
import { Link } from "gatsby";
import * as React from "react";
import { useUserInfo } from "../../../components/save";
import Defer from "../../../components/ui/Defer";
import Navbar from "../../../components/ui/Navbar";
import Ambulan from '@material-ui/icons/LocalHospital';
import Kematian from '@material-ui/icons/AirlineSeatFlat';
import Kecelakaan from '@material-ui/icons/Commute';
import Kerusakan from '@material-ui/icons/DomainDisabled';
import Kejahatan from '@material-ui/icons/Warning';
import Bencana from '@material-ui/icons/Pool';
import Call from '@material-ui/icons/Call';
import { useModalTheme } from "../../../components/style";

const options = [{
    type: 'ambulan',
    label: 'Ambulan',
    desc: 'Layanan untuk memanggil ambulan desa untuk keperluan antar jemput',
    icon: Ambulan
}, {
    type: 'kematian',
    label: 'Kematian',
    desc: 'Layanan untuk menanggapi laporan warga yang meninggal dunia',
    icon: Kematian
}, {
    type: 'kecelakaan',
    label: 'Kecelakaan',
    desc: 'Layanan untuk merespon kecelakaan rumah tangga atau lalu lintas',
    icon: Kecelakaan
}, {
    type: 'kerusakan',
    label: 'Kerusakan',
    desc: 'Layanan untuk merespon kerusakan insfrasuktur publik',
    icon: Kerusakan
}, {
    type: 'kejatahan',
    label: 'Kejahatan',
    desc: 'Layanan untuk merespon tindak kejahatan di pemukiman warga',
    icon: Kejahatan
}, {
    type: 'bencana',
    label: 'Bencana',
    desc: 'Layanan untuk merespon bencana alam, longsor atau kebarakan',
    icon: Bencana
}
];


const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const modalTheme = useModalTheme();
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            {open && <Paper className={modalTheme.paper} elevation={5} style={{ textAlign: 'center' }}><List>
                <ListItem className={modalTheme.center}>
                    {React.createElement(inquiry.icon, { style: { fontSize: '2em', marginRight: 15 } })}
                    <Typography>{inquiry.label}</Typography>
                </ListItem>
                <ListItem className={modalTheme.center}>
                    <IconButton onClick={onAction}>
                        <Call style={{ fontSize: '6em' }} />
                    </IconButton>
                </ListItem>
                <ListItem className={modalTheme.center}>
                    <Typography>Anda akan menghubungi pihak perangkat desa untuk pelayanan {inquiry.label}.<br />
                        Klik tombol panggil diatas untuk melakukan panggilan.
                        Mohon menggunakan layanan ini dengan bijak.</Typography>
                </ListItem>
                <ListItem className={modalTheme.center}>
                    <Button onClick={onClose} variant="outlined">Batal</Button>
                </ListItem>
            </List></Paper>}
        </Modal>
    );
}

const Page = () => {
    const user = useUserInfo();
    const [page, setPage] = React.useState(0);
    const inquiry = React.useRef(null);
    const confirm = (idx) => {
        inquiry.current = options[idx];
        setPage(1);
    }
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/home/">
                            Layanan Desa
                        </Link>
                        <Typography color="textPrimary">Layanan Darurat</Typography>
                    </Breadcrumbs>
                </ListItem>
                <Defer data={user}>
                    {() => (
                        user.features.emergency ?
                            <>
                                <ListItem>
                                    <Typography variant="h5">
                                        Layanan Darurat Desa {user.geolookup.kelurahan}
                                    </Typography>
                                </ListItem>
                                {
                                    options.map((x, k) => <ListItem key={x.type} button onClick={() => confirm(k)}>
                                        {React.createElement(x.icon, { style: { fontSize: '2em', marginRight: 15 } })}
                                        <ListItemText primary={x.label} secondary={x.desc} />
                                    </ListItem>)
                                }
                            </>
                            :
                            <ListItem>
                                <ListItemText primary={`Layanan Darurat untuk Desa ${user.geolookup.kelurahan} Belum Tersedia`} />
                            </ListItem>
                    )}
                </Defer>
                <Prompt open={page === 1} onClose={() => setPage(0)} onAction={() => { }} inquiry={inquiry.current} />
            </List>
        </div>
    );
}

export default Page
